import Head from 'next/head';
import { CircleLoader } from 'react-spinners';
import { useState, useEffect } from 'react';
import ItemPageStyles from '../components/styles/ItemPageStyles';
import Layout from '../components/Layout';
import data from '../static/data';
import ProjectCard from '../components/ProjectCard';
import { products } from '../static/data';

const Item = ({ query }) => {
  const [loading, setLoading] = useState(true);
  const [item, setItem] = useState({});
  const calculateMoney = amount => `$${amount / 100}`;

  useEffect(() => {
    setLoading(true);
    const pageItem = products.filter(product => {
      console.log(product.id);
      console.log(query.id);
      return product.id === query.id;
    });
    setItem({ ...pageItem[0] });
    console.log(pageItem[0]);
    setTimeout(() => {
      setLoading(false);
    }, 1000);
  }, []);

  if (loading) {
    return (
      <Layout>
        <ItemPageStyles>
          <div className='loading-page'>
            <h1>
              Loading <span>MULO</span> Product
            </h1>
            <div className='spinner'>
              <CircleLoader sizeUnit='px' size={200} color='#17D3D3' />
            </div>
          </div>
        </ItemPageStyles>
      </Layout>
    );
  }
  return (
    <Layout>
      <Head>
        <title>Theran Brigowatz | Portfolio</title>
      </Head>
      <ItemPageStyles>
        {item.name === null ? (
          <h1>Oops...Item Not Found</h1>
        ) : (
          <>
            <h1>{item.name}</h1>
            <div className='item-information'>
              <div className='left-content'>
                <img src={item.image} />
                <p>{item.about}</p>
              </div>
              <div>
                <h3>
                  Original Price: <span>{calculateMoney(item.originalPrice)}</span>
                </h3>
                <h3>Discounted Price: {calculateMoney(item.price)}</h3>
                {!item.problems.length ? (
                  <div className='defects'>
                    <h3>No Defects</h3>
                  </div>
                ) : (
                  <div className='defects'>
                    <h3>Defects:</h3>
                    <ul>
                      {item.problems.map(problem => (
                        <li>{problem}</li>
                      ))}
                    </ul>
                  </div>
                )}
                <div className='buttons'>
                  <button className="add-button">Add To Cart</button>
                  <button disabled={!item.canOffer}>Make An Offer</button>
                </div>
              </div>
            </div>
          </>
        )}
      </ItemPageStyles>
    </Layout>
  );
};

export default Item;
